import React, { useEffect } from "react";
import { Field, useFormikContext } from "formik";
import { Stack, Typography } from "@material-ui/core";
import { styled } from "@material-ui/styles";

import { goalFormModel } from "../../../../../utils/goal-model";
import { CreateGoalType, getPercentages, RootAppState } from "../../../../../utils/typeHelpers";
import CustomInputField from "../Components/CustomInputField";
import DisabledTextField from "../Components/DisabledTextField";

import { useSelector } from "react-redux";
import useAxios from "../../../../../utils/useAxios";
import { useQuery, UseQueryResult } from "react-query";
// ----------------------------------------------------------------------

export default function Step2() {
  const {
    formField: {
      product,
      productlink,
      productunitcost,
      productunitnumber,
      productsubtotal,
      // productservicefee,
      productgoaltotal,

      productpgwfee,
      productngofee,
      productplatformfee,
    },
  } = goalFormModel;
  const { values } = useFormikContext<CreateGoalType>();
  const [result, setResult] = React.useState("");
  // useEffect(() => {
  //   setResult(URL.createObjectURL(values.file));

  //   // eslint-disable-next-line react-hooks/exhaustive-deps
  // }, [values.file]);

  const profile_type = useSelector(
    (state: RootAppState) => state.AuthReducer.userInfo.user_profile?.profile_type
  );

  let axiosInstance = useAxios();
  const {
    data,
    isLoading,
    isError,
    error,
  }: UseQueryResult<getPercentages[], Error> = useQuery(
    ["getPercentages"],
    async () => {
      const response = await axiosInstance.get(`/get-percentages/`);

      return response.data;
    }
  );
  const percentage = data ? data[0] : '';
  const pgw = percentage ? percentage.pgw : 0;
  let ngo = percentage ? percentage.ngo : 0;
  if ( profile_type === 'NGO' ) {
    ngo = 0;
  }
  const platform = percentage ? percentage.platform : 0;


  return (
    <Stack
      direction="column"
      justifyContent="center"
      alignItems="center"
      spacing={3}
      sx={{ padding: 5, width: 350 }}
    >
      {/* <ProdImage src={result} alt="prodform" /> */}
      <Typography variant="subtitle1">{values.goalname}</Typography>
      <Typography variant="caption">{values.shortGoal}</Typography>
      {/* <SdlsList topic={values.topic} /> */}
      <Field
        name={product.name}
        component={CustomInputField}
        label={product.label}
        type="text"
      />
      <Field
        name={productlink.name}
        component={CustomInputField}
        label={productlink.label}
        type="text"
      />
      <Field
        name={productunitcost.name}
        component={CustomInputField}
        label={productunitcost.label}
        type="number"
      />
      <Field
        name={productunitnumber.name}
        component={CustomInputField}
        label={productunitnumber.label}
        type="number"
      />
      <Field
        name={productsubtotal.name}
        component={DisabledTextField}
        label={productsubtotal.label}
        type="number"
      />
      {/* <Field
        name={productservicefee.name}
        component={DisabledTextField}
        label={productservicefee.label}
        type="number"
      /> */}
      {/* ====================== new ========================== */}
      <Field
        name={productpgwfee.name}
        component={DisabledTextField}
        label={`PGW fee ${pgw}%`}
        type="number"
      />

      <Field
        name={productngofee.name}
        component={DisabledTextField}
        label={`NGO fee ${ngo}%`}
        type="number"
      />

      <Field
        name={productplatformfee.name}
        component={DisabledTextField}
        label={`Platform fee ${platform}%`}
        type="number"
      />

      {/* ======================== end =========================== */}
      <Field
        name={productgoaltotal.name}
        component={DisabledTextField}
        label={productgoaltotal.label}
        type="number"
      />
    </Stack>
  );
}

const ProdImage = styled("img")({
  height: 120,
  width: 80,
});
