import {
  Button,
  CircularProgress,
  Stack,
  Container,
  Paper,
  AlertColor,
} from "@material-ui/core";
import React, { useState, useEffect, SyntheticEvent, useRef } from "react";

import { Form, Formik, FormikHelpers } from "formik";

import CustomStepper from "./Components/CustomStepper";

import { Step1, Step2, Step3 } from "./Steps";
import { Box } from "@material-ui/system";

import {
  AddFunction,
  CreateGoalResponse,
  CreateGoalType,
  RootAppState,
  StepType,
} from "../../../../utils/typeHelpers";
import { createGoalInitialValues } from "../../../../utils/initialvalues";
import { validationSchemaGoal } from "../../../../utils/formSchemas";
import { useMutation, UseMutationResult } from "react-query";
import { CREATE_GOAL_API } from "../../../../utils/Api";
import { useSelector } from "react-redux";
import { useNavigate } from "react-router";

import Loader from "../../../../components/Loader";
import { MessageError } from "../../../../components/AlertMessageError";
import { MessageSuccess } from "../../../../components/AlertMessageSuccess";

const CreateGoal = () => {
  const refError = useRef<null | AddFunction>(null);
  const refSuccess = useRef<null | AddFunction>(null);

  const navigate = useNavigate();
  const accesstoken = useSelector(
    (state: RootAppState) => state.AuthReducer.userInfo.access
  );

  //api mutation
  const mutationCreateGoal: UseMutationResult<
    CreateGoalResponse,
    Error,
    CreateGoalType
  > = useMutation((goal: CreateGoalType) => CREATE_GOAL_API(goal, accesstoken));
  const [initialvalue, setInitialValue] = useState<CreateGoalType>(
    createGoalInitialValues
  );
  const { isLoading, isError, isSuccess, data } = mutationCreateGoal;
  const [activeStep, setActiveStep] = useState(0);

  const steps: StepType[] = [
    {
      label: "Goal Information",

      done: true,
    },

    {
      label: "Goal Description",

      done: false,
    },
    {
      label: "Goal Video",

      done: false,
    },
  ];

  const selectedValidationSchema = validationSchemaGoal[activeStep];
  const isLast = activeStep === 3 - 1;

  const sleep = (time: number) => {
    return new Promise((resolve) => setTimeout(resolve, time));
  };

  const submitForm = async (
    values: CreateGoalType,
    actions: FormikHelpers<CreateGoalType>
  ) => {
    await sleep(200);
    setInitialValue(values);
    // console.log(values);

    mutationCreateGoal.mutate(values);
    actions.setSubmitting(false);
  };

  const handleSubmit = (
    values: CreateGoalType,
    actions: FormikHelpers<CreateGoalType>
  ) => {
    if (isLast) {
      submitForm(values, actions);
    } else {
      setActiveStep((prev) => prev + 1);
      actions.setTouched({});
      actions.setSubmitting(false);
    }
  };

  const handleCreateError = (msg: string) => {
    refError.current?.(msg);
  };

  const handleCreateSucess = (msg: string) => {
    refSuccess.current?.(msg);
  };

  useEffect(() => {
    let didCancel = false;

    if (isSuccess) {
      if (data !== undefined) {
        if (
          typeof data.pgw_amount === "string" &&
          typeof data.title === "string"
        ) {
          handleCreateSucess("Goal Creation Successful");
          setTimeout(() => {
            navigate("../");
          }, 3000);
        } else {
          handleCreateError("Goal Creation Failed");
        }
      }
    }

    return () => {
      didCancel = true;
    };
  }, [isError, isSuccess]);

  if (isLoading) {
    return <Loader />;
  }
  return (
    <div>
      <MessageError
        children={(add: AddFunction) => {
          refError.current = add;
        }}
      />
      <MessageSuccess
        children={(add: AddFunction) => {
          refSuccess.current = add;
        }}
      />
      <Container component="main" maxWidth="sm" sx={{ mb: 4 }}>
        <Paper
          variant="outlined"
          sx={{ my: { xs: 3, md: 6 }, p: { xs: 2, md: 3 } }}
        >
          <CustomStepper activeStep={activeStep} steps={steps} />

          <Formik
            initialValues={initialvalue}
            validationSchema={selectedValidationSchema}
            onSubmit={handleSubmit}
          >
            {(formikProps) => (
              <Form>
                <Stack spacing={2}>
                  {renderStepContent(activeStep)}
                  <Box sx={{ display: "flex", justifyContent: "flex-end" }}>
                    {activeStep > 0 && (
                      <Button onClick={() => setActiveStep((prev) => prev - 1)}>
                        Back
                      </Button>
                    )}

                    <Button
                      type="submit"
                      variant="contained"
                      color="primary"
                      disabled={formikProps.isSubmitting}
                    >
                      {isLast ? "Create Goal" : "Next"}
                    </Button>
                    {formikProps.isSubmitting && <CircularProgress />}
                  </Box>
                </Stack>
              </Form>
            )}
          </Formik>
        </Paper>
      </Container>
    </div>
  );
};

export default CreateGoal;

export const renderStepContent = (step: number) => {
  switch (step) {
    case 0:
      return <Step1 />;
    case 1:
      return <Step2 />;
    case 2:
      return <Step3 />;
  }
};
