import React from "react";
import { InputAdornment, TextField } from "@material-ui/core";
import { FieldProps } from "formik";
import { Icon } from "@iconify/react";
import DollarIcon from "@iconify/icons-ant-design/dollar-outlined";
import palette from "../../../../../theme/palette";
interface DisabledTextFieldProps extends FieldProps {
  label?: string;
}

const DisabledTextField = ({ field, form, label }: DisabledTextFieldProps) => {
  const { touched, errors } = form;
  const renderHelperText = () => {
    if (touched[field.name] && errors[field.name]) {
      return errors[field.name];
    }
  };

  return (
    <TextField
      fullWidth
      variant="filled"
      disabled
      helperText={renderHelperText()}
      error={Boolean(touched[field.name] && errors[field.name])}
      label={label}
      InputProps={{
        startAdornment: (
          <InputAdornment position="start">
            <Icon
              icon={DollarIcon}
              color={palette.success.main}
              fontSize={20}
            />
          </InputAdornment>
        ),
      }}
      {...field}
    />
  );
};

export default DisabledTextField;
