import React from "react";
import { Field } from "formik";
import { Stack } from "@material-ui/core";

import { goalFormModel } from "../../../../../utils/goal-model";
import BioInputFieldProfile from "../../../../Authentication/Components/BioInputField";
import VideoInput from "../Components/VideoInput";

// ----------------------------------------------------------------------

export default function Step3() {
  const {
    formField: { videofile, full_description },
  } = goalFormModel;

  return (
    <Stack
      direction="column"
      justifyContent="center"
      alignItems="center"
      spacing={3}
      sx={{ padding: 5, width: 350 }}
    >
      <Field
        name={videofile.name}
        component={VideoInput}
        label={videofile.label}
        type="input"
      />
      <Field
        name={full_description.name}
        component={BioInputFieldProfile}
        label={full_description.label}
      />
    </Stack>
  );
}
