import React, { useEffect } from "react";
import { useDropzone } from "react-dropzone";
import { useFormikContext, FieldProps } from "formik";

import { styled } from "@material-ui/styles";
import { Icon } from "@iconify/react";
import imageFill from "@iconify/icons-ant-design/file-image-outlined";
import { Stack, Typography, Box } from "@material-ui/core";

import palette from "../../../../../theme/palette";
import {
  CreateGoalType,
  CreateProfileTypeNgoOrDonee,
  RootAppState,
} from "../../../../../utils/typeHelpers";
import { useQuery, UseQueryResult } from "react-query";
import { useSelector } from "react-redux";
// import { GET_DONEEORNGO_PROFILE_API } from "../../../../../utils/Api";

import ErrorCatchComponent from "../../../../../components/ErrorCatchComponent";
import ApiErrorPage from "../../../../Errror/ApiErrorPage";
import Loader from "../../../../../components/Loader";
import useAxios from "../../../../../utils/useAxios";

const InputImage = ({ field, form }: FieldProps) => {
  const accesstoken = useSelector(
    (state: RootAppState) => state.AuthReducer.userInfo.access
  );

  // const {
  //   data,
  //   isSuccess,
  // }: UseQueryResult<CreateProfileTypeNgoOrDonee, Error> = useQuery(
  //   ["doneNgoInfo", accesstoken],
  //   () => GET_DONEEORNGO_PROFILE_API(accesstoken)
  // );

  const axiosInstance = useAxios();
  const {
    data,
    isLoading,
    isError,
    error,
    isSuccess,
  }: UseQueryResult<CreateProfileTypeNgoOrDonee, Error> = useQuery(
    ["doneNgoInfo", accesstoken],
    async () => {
      const response = await axiosInstance.get(`/profile-detail/`);
      return response.data;
    }
  );

  const { acceptedFiles, getRootProps, getInputProps } = useDropzone({
    maxFiles: 1,
  });
  const { errors, isValid } = form;

  const { setFieldValue } = useFormikContext<CreateGoalType>();
  const [result, setResult] = React.useState("");

  useEffect(() => {
    setFieldValue("file", acceptedFiles[0]);
    if (acceptedFiles.length !== 0) {
      if (data?.id !== null) {
        form.setFieldValue("profile", data?.id);
      }
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [acceptedFiles, isValid]);

  useEffect(() => {
    if (field.value) {
      setResult(URL.createObjectURL(field.value));
    }
  }, [field.value]);

  // console.log('GET_DONEEORNGO_PROFILE_API input image: ##')

  return (
    <ErrorCatchComponent>
      {isLoading === false ? (
        <div>
          {isError ? (
            <ApiErrorPage
              error={error ? error : new Error("Network response was not ok.")}
            />
          ) : (
            <Stack
              direction="column"
              justifyContent="center"
              alignItems="center"
              spacing={1}
              {...getRootProps({ className: "dropzone" })}
            >
              <div>
                <input {...getInputProps()} />
                <BoxImage>
                  {result !== "" ? (
                    <ProdImage src={result} alt="prodform" />
                  ) : (
                    <Icon
                      icon={imageFill}
                      fontSize={80}
                      color={palette.primary.main}
                    />
                  )}
                </BoxImage>
              </div>
              <Typography variant="subtitle1">
                Drag and drop or <SpanText>Browse Goal Picture</SpanText>
              </Typography>
              {errors[field.name] && (
                <ErrorText variant="body1">{errors[field.name]}</ErrorText>
              )}
            </Stack>
          )}
        </div>
      ) : (
        <Loader />
      )}
    </ErrorCatchComponent>
  );
};

export default InputImage;

const BoxImage = styled(Box)({
  width: 120,
  height: 120,
  justifyContent: "center",
  alignItems: "center",
  display: "flex",
});
const ErrorText = styled(Typography)({
  color: "red",
  fontSize: 12,
});
const ProdImage = styled("img")({
  height: 120,
  width: 80,
});
const SpanText = styled("span")({
  color: "#007AFF",
});
