import React, { useEffect, useState } from "react";
// material
import { Stack } from "@material-ui/core";

import { Field } from "formik";

import CustomInputField from "../Components/CustomInputField";
import MultiLineInput from "../Components/MultiLineInput";
import { OptionType } from "../utils/data";

import InputImage from "../Components/InputImage";
import { goalFormModel } from "../../../../../utils/goal-model";
import { useQuery, UseQueryResult } from "react-query";
import { RootAppState, SdgsType } from "../../../../../utils/typeHelpers";
import { useSelector } from "react-redux";
// import { GET_SDGS_API } from "../../../../../utils/Api";
import Loader from "../../../../../components/Loader";
import CustomSelectGoal from "../Components/CustomSelectgoal";

import useAxios from "../../../../../utils/useAxios";
import ErrorCatchComponent from "../../../../../components/ErrorCatchComponent";
import ApiErrorPage from "../../../../Errror/ApiErrorPage";

// ----------------------------------------------------------------------

export default function Step1() {
  const {
    formField: { file, shortGoal, goalname },
  } = goalFormModel;

  const accesstoken = useSelector(
    (state: RootAppState) => state.AuthReducer.userInfo.access
  );

  // const {
  //   data: optionsSdgs,
  //   isLoading: isLoadingSdgs,
  // }: UseQueryResult<SdgsType[], Error> = useQuery(
  //   ["getSdgs", accesstoken],
  //   () => GET_SDGS_API(accesstoken)
  // );

  const axiosInstance = useAxios();
  const { data:optionsSdgs, isLoading:isLoadingSdgs, isError:isErrorSdgs, error:errorSdgs }: UseQueryResult<SdgsType[], Error> = useQuery(
    ["getSdgs", accesstoken],
    async () => {
      const response = await axiosInstance.get(`/sdgs-list/`);

      return response.data;
    }
  );

  const [sdgs, setSdgs] = useState<OptionType[]>([]);

  useEffect(() => {
    if (optionsSdgs) {
      if (optionsSdgs?.length > 1) {
        const newArr: OptionType[] = optionsSdgs.map((item: SdgsType) => {
          return {
            value: item.id,
            label: item.title,
          };
        });
        setSdgs(newArr);
      }
    }
  }, [optionsSdgs]);

  if (isLoadingSdgs) {
    return <Loader />;
  }

  // console.log(optionsSdgs);

  return (
    <ErrorCatchComponent>
    {isLoadingSdgs === false ? (
      <div>
        {isErrorSdgs ? (
          <ApiErrorPage
            error={errorSdgs ? errorSdgs : new Error("Network response was not ok.")}
          />
        ) : (
         
          <Stack spacing={3}>
            <Field
              name={file.name}
              component={InputImage}
              label={file.label}
              type="input"
            />

            <Field
              name={goalname.name}
              component={CustomInputField}
              label={goalname.label}
              type="text"
            />
            <MultiLineInput
              defaultProps={{
                name: shortGoal.name,
                label: shortGoal.label,
                fullWidth: true,
              }}
            />
            {sdgs.length > 0 && (
              <Field
                name="sdgs"
                options={sdgs}
                component={CustomSelectGoal}
                placeholder="Select SDGS"
                isMulti={true}
              />
            )}
          </Stack>
          
        )}
      </div>
    ) : (
      <Loader />
    )}
  </ErrorCatchComponent>
  
  );
}
