import React, { useEffect } from "react";
import { TextField } from "@material-ui/core";
import { FieldProps, useFormikContext } from "formik";
import {
  CreateGoalType,
  getPercentages,
  RootAppState,
} from "../../../../../utils/typeHelpers";
import useAxios from "../../../../../utils/useAxios";
import { useQuery, UseQueryResult } from "react-query";
import Loader from "../../../../../components/Loader";
import ApiErrorPage from "../../../../Errror/ApiErrorPage";
import { useSelector } from "react-redux";

interface CustomInputFieldProps extends FieldProps {
  label?: string;
  type?: string;
}

const CustomInputField = ({
  field,
  form,
  label,
  type,
}: CustomInputFieldProps) => {
  const { touched, errors } = form;
  const renderHelperText = () => {
    if (touched[field.name] && errors[field.name]) {
      return errors[field.name];
    }
  };
  const { values, setFieldValue } = useFormikContext<CreateGoalType>();

  const profile_type = useSelector(
    (state: RootAppState) =>
      state.AuthReducer.userInfo.user_profile?.profile_type
  );

  let axiosInstance = useAxios();
  const {
    data,
    isLoading,
    isError,
    error,
  }: UseQueryResult<getPercentages[], Error> = useQuery(
    ["getPercentages"],
    async () => {
      const response = await axiosInstance.get(`/get-percentages/`);

      return response.data;
    }
  );
  const percentage = data ? data[0] : "";
  const pgw = percentage ? percentage.pgw : 0;
  let ngo = percentage ? percentage.ngo : 0;
  if (profile_type === "NGO") {
    ngo = 0;
  }
  const platform = percentage ? percentage.platform : 0;
  // console.log(ngo, ' -- ', platform);

  useEffect(() => {
    let productsubtotal = values.unitnumber * values.unitcost;
    // let productservicefee = (productsubtotal * 10) / 100;

    let productPgwFee = parseFloat(((productsubtotal * pgw) / 100).toFixed(2));
    let productNgoFee = +((productsubtotal * ngo) / 100).toFixed(2);
    let productPlatformFee = +((productsubtotal * platform) / 100).toFixed(2);

    // let productgoaltotal = productsubtotal + productservicefee;
    let productgoaltotal = +(
      productsubtotal +
      productPgwFee +
      productNgoFee +
      productPlatformFee
    ).toFixed(2);

    setFieldValue("productsubtotal", productsubtotal);
    // setFieldValue("productservicefee", productservicefee);

    setFieldValue("productpgwfee", productPgwFee);
    setFieldValue("productngofee", productNgoFee);
    setFieldValue("productplatformfee", productPlatformFee);

    setFieldValue("productgoaltotal", productgoaltotal);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [values.unitnumber, values.unitcost]);
  return (
    <TextField
      fullWidth
      variant="outlined"
      type={type}
      helperText={renderHelperText()}
      error={Boolean(touched[field.name] && errors[field.name])}
      label={label}
      {...field}
    />
  );
};

export default CustomInputField;
