import React, { useEffect, useState } from "react";
import { useDropzone } from "react-dropzone";
import { useFormikContext, FieldProps } from "formik";

import { styled } from "@material-ui/styles";
import { Icon } from "@iconify/react";
import imageFill from "@iconify/icons-ant-design/cloud-upload";
import { Stack, Typography, Box } from "@material-ui/core";
import palette from "../../../../../theme/palette";
import { CreateGoalType } from "../../../../../utils/typeHelpers";

const VideoInput = ({ field, form }: FieldProps) => {
  const { acceptedFiles, getRootProps, getInputProps } = useDropzone({
    maxFiles: 5,
  });
  const { errors: errorform, isValid } = form;
  const [error, SetErros] = useState<string | null>(null);

  const { setFieldValue } = useFormikContext<CreateGoalType>();

  const loadVideo = (file: File) =>
    new Promise((resolve, reject) => {
      try {
        let video = document.createElement("video");
        video.preload = "metadata";

        video.onloadedmetadata = function () {
          resolve(this);
        };

        video.onerror = function () {
          reject("Invalid video. Please select a video file.");
        };

        video.src = window.URL.createObjectURL(file);
      } catch (e) {
        reject(e);
      }
    });

  const load = async () => {
    try {
      acceptedFiles.map(async (file) => {
        const video: any = await loadVideo(file);

        if (video.duration >= 20) {
          SetErros("Video duration must be less than 20 or equal 20 seconds");
        } else {
          SetErros(null);
          setFieldValue("videofile", acceptedFiles);
        }
      });
    } catch (er) {}
  };
  useEffect(() => {
    load();
  }, [acceptedFiles]);

  return (
    <Stack
      direction="column"
      justifyContent="center"
      alignItems="center"
      spacing={3}
    >
      <div {...getRootProps({ className: "dropzone" })}>
        <input {...getInputProps()} />
        <BoxImage>
          <Icon icon={imageFill} fontSize={50} color="#007AFF" />
          <Typography variant="subtitle1">
            Drag and Drop{" "}
            <SpanText>
              Browse a unique 20 sec video of your goal to make it viral!
            </SpanText>
          </Typography>
          <Typography variant="caption" color={palette.grey[600]}>
            Max Duration 20 seconds and Size 100 MB
          </Typography>
          {field.value &&
            field.value.length > 0 &&
            error === null &&
            field.value.map((item: any) => (
              <Typography variant="caption" color={palette.warning.dark}>
                {item.name}
              </Typography>
            ))}
          {error !== null && <ErrorText>{error}</ErrorText>}
          {errorform.videofile && <ErrorText>{errorform.videofile}</ErrorText>}
        </BoxImage>
      </div>
    </Stack>
  );
};

const BoxImage = styled(Box)({
  backgroundColor: "#CED4DA",
  width: 300,
  minHeight: 150,
  borderRadius: 11,
  justifyContent: "center",
  alignItems: "center",
  display: "flex",
  flexDirection: "column",
});
const ErrorText = styled(Typography)({
  color: "red",
  fontSize: 12,
});
const SpanText = styled("span")({
  color: "#007AFF",
});

export default VideoInput;
